import JsEvent from '@rotomeca/event';
import { Nullable } from '../utils/types';

const listenersCacheKey = Symbol('listenersCache');

export function Listener<TCallback extends Function, This = any>(
  initilizator?: Nullable<(event: JsEvent<TCallback>, instance: This) => void>,
) {
  return function (
    _target: ClassAccessorDecoratorTarget<This, JsEvent<TCallback>>,
    context: ClassAccessorDecoratorContext<This, JsEvent<TCallback>>,
  ): ClassAccessorDecoratorResult<This, JsEvent<TCallback>> {
    const methodName = String(context.name);
    const listenerCacheKey = Symbol(`listener_${methodName}`);

    return {
      get(this: This): JsEvent<TCallback> {
        const self = this as any;

        if (!self[listenersCacheKey])
          self[listenersCacheKey] = new Map<Symbol, JsEvent<any>>();

        if (self[listenersCacheKey].has(listenerCacheKey)) {
          return self[listenersCacheKey].get(listenerCacheKey);
        }

        const event = new JsEvent<TCallback>();
        if (initilizator) {
          initilizator(event, this);
        }

        self[listenersCacheKey].set(listenerCacheKey, event);

        return self[listenersCacheKey].get(listenerCacheKey);
      },
    };
  };
}
