import { Define } from '../../../core/decorators';
import { TAG_COLUMN } from '../../../core/utils/constants/tags';
import { Nullable } from '../../../core/utils/types';
import BnumElement, { CustomDom } from '../../bnum-element';

/**
 * Type représentant les slots possibles d'une colonne.
 * - 'header' : pour l'en-tête
 * - 'body' : pour le corps
 * - 'footer' : pour le pied de page
 */
export type ColumnSlot = 'header' | 'body' | 'footer';

/**
 * Tag HTML personnalisé interne utilisé pour ce composant.
 *
 * Source de vérité pour la lisibilités des autres constantes liées à la classe et au tag de celle-ci.
 */
const COMPONENT_TAG = TAG_COLUMN;

/**
 *  Permet de structurer une colonne avec un en-tête, un corps et un pied de page.
 *
 * @structure Colonne
 * <bnum-column>
 *  <div slot="header">En-tête de la colonne</div>
 *   <div>Contenu principal de la colonne</div>
 *  <div slot="footer">Pied de page de la colonne</div>
 * </bnum-column>
 */
@Define()
export class HTMLBnumColumn extends BnumElement {
  //#region Constantes statiques

  /**
   * Tag HTML personnalisé utilisé pour ce composant.
   */
  static get TAG(): typeof COMPONENT_TAG {
    return COMPONENT_TAG;
  }

  /**
   * Nom de l'attribut pour le type de colonne.
   * @attr {string} (optional) (default: 'default') type - Le type de colonne (ex: "sidebar", "main", "tools")
   */
  static readonly ATTR_TYPE = 'type';

  /**
   * Valeur par défaut pour le type de colonne.
   */
  static readonly DEFAULT_COLUMN_TYPE = 'default';

  /**
   * Préfixe commun pour les classes CSS de la colonne.
   */
  static readonly CLASS_PREFIX = this.TAG;

  /**
   * Classe CSS pour l'en-tête de la colonne.
   */
  static readonly CLASS_HEADER = `${this.CLASS_PREFIX}__header` as const;

  /**
   * Classe CSS "legacy" pour l'en-tête (compatibilité).
   */
  static readonly CLASS_RC_HEADER = 'header';

  /**
   * Ancienne classe CSS pour l'en-tête (pour rétrocompatibilité).
   */
  static readonly CLASS_RC_HEADER_OLD = `old-${this.CLASS_RC_HEADER}` as const;

  /**
   * Classe CSS pour le corps de la colonne.
   */
  static readonly CLASS_BODY = `${this.CLASS_PREFIX}__body` as const;

  /**
   * Classe CSS pour le pied de page de la colonne.
   */
  static readonly CLASS_FOOTER = `${this.CLASS_PREFIX}__footer` as const;

  /**
   * Classe CSS "legacy" pour le pied de page (compatibilité).
   */
  static readonly CLASS_RC_FOOTER = 'footer';

  /**
   * Classe CSS indiquant qu'un élément provient d'un slot.
   */
  static readonly CLASS_FROM_SLOT = 'from-slot';

  /**
   * Préfixe pour les classes CSS de contenu.
   */
  static readonly CLASS_CONTENT_PREFIX = this.CLASS_PREFIX;

  /**
   * Suffixe pour les classes CSS de contenu.
   */
  static readonly CLASS_CONTENT_POSTFIX = 'content';

  /**
   * Classe CSS pour le contenu de l'en-tête.
   */
  static readonly CLASS_CONTENT_HEADER =
    `${this.CLASS_CONTENT_PREFIX}__header__${this.CLASS_CONTENT_POSTFIX}` as const;

  /**
   * Classe CSS pour le contenu du corps.
   */
  static readonly CLASS_CONTENT_BODY =
    `${this.CLASS_CONTENT_PREFIX}__body__${this.CLASS_CONTENT_POSTFIX}` as const;

  /**
   * Classe CSS pour le contenu du pied de page.
   */
  static readonly CLASS_CONTENT_FOOTER =
    `${this.CLASS_CONTENT_PREFIX}__footer__${this.CLASS_CONTENT_POSTFIX}` as const;

  /**
   * Nom du slot pour l'en-tête.
   */
  static readonly SLOT_HEADER = 'header';

  /**
   * Nom du slot pour le pied de page.
   */
  static readonly SLOT_FOOTER = 'footer';

  /**
   * Nom de l'attribut de données pour conserver le corps.
   * @attr {boolean} (optional) (default: true) data-keep-body - Indique si le corps doit être conservé
   */
  static readonly DATA_KEEP_BODY = 'keep-body';
  //#endregion Constantes statiques

  //#region Constants Map

  /**
   * Regroupe les différentes classes CSS utilisées par le composant.
   * @private
   */
  static readonly #_CLASSES = {
    HOST: this.TAG,
    HEADER: {
      MAIN: this.CLASS_HEADER,
      RC: this.CLASS_RC_HEADER,
      OLD: this.CLASS_RC_HEADER_OLD,
    },
    BODY: this.CLASS_BODY,
    FOOTER: {
      MAIN: this.CLASS_FOOTER,
      RC: this.CLASS_RC_FOOTER,
    },
    CONTENT_PREFIX: this.TAG,
    FROM_SLOT: this.CLASS_FROM_SLOT,
    CONTENT: {
      HEADER: this.CLASS_CONTENT_HEADER,
      BODY: this.CLASS_CONTENT_BODY,
      FOOTER: this.CLASS_CONTENT_FOOTER,
    },
  } as const;

  /**
   * Regroupe les noms de slots utilisés.
   * @private
   */
  static readonly #_SLOTS = {
    HEADER: this.SLOT_HEADER,
    FOOTER: this.SLOT_FOOTER,
  } as const;

  /**
   * Regroupe les noms d'attributs utilisés.
   * @private
   */
  static readonly #_ATTRIBUTES = {
    TYPE: this.ATTR_TYPE,
    DATA: {
      KEEP_BODY: this.DATA_KEEP_BODY,
    },
  } as const;
  //#endregion Constants Map

  //#region Getters/Setters
  /**
   * Permet de définir le type de colonne (ex: "sidebar", "main", "tools")
   * Utile pour le CSS qui va définir la largeur
   */
  get type(): string {
    return (
      this.getAttribute(HTMLBnumColumn.#_ATTRIBUTES.TYPE) ||
      HTMLBnumColumn.DEFAULT_COLUMN_TYPE
    );
  }

  /**
   * Indique si le corps de la colonne doit être conservé lors de certaines opérations.
   *
   * Rappel: data- ne sert qu'à stocker des informations avant la création du composant.
   */
  get #_keepBody(): boolean {
    return this.data(HTMLBnumColumn.#_ATTRIBUTES.DATA.KEEP_BODY) === 'true';
  }
  //#endregion Getters/Setters

  //#region LifeCycle

  /**
   * Constructeur de la colonne Bnum.
   */
  constructor() {
    super();
  }

  /**
   * On désactive le shadow-dom pour cette élément.
   * @protected
   */
  protected _p_isShadowElement(): boolean {
    return false;
  }

  /**
   * Logique de rendu Light DOM
   * On récupère les enfants existants et on les réorganise.
   * @param container Le conteneur dans lequel injecter le DOM reconstruit
   * @protected
   */
  protected _p_buildDOM(container: CustomDom): void {
    // Sauvegarde des enfants actuels
    const originalChildren = Array.from(this.childNodes);

    // Fragment temporaire pour construire le DOM avant injection
    const fragment = document.createDocumentFragment();

    // Création des conteneurs
    const [headerContainer, bodyContainer, footerContainer] =
      this._p_createDivs(
        {
          classes: [
            HTMLBnumColumn.#_CLASSES.HEADER.MAIN,
            HTMLBnumColumn.#_CLASSES.HEADER.RC,
          ],
        },
        {
          classes: [HTMLBnumColumn.#_CLASSES.BODY],
        },
        {
          classes: [
            HTMLBnumColumn.#_CLASSES.FOOTER.MAIN,
            HTMLBnumColumn.#_CLASSES.FOOTER.RC,
          ],
        },
      );

    // Distribution des enfants (Slotting manuel)
    let hasHeader = false;
    let hasFooter = false;

    for (const node of originalChildren) {
      // Si c'est un noeud texte vide, on ignore
      if (node.nodeType === Node.TEXT_NODE) {
        if (node.textContent?.trim()) bodyContainer.appendChild(node);
        continue;
      }

      const nodeElement: Nullable<HTMLElement> =
        node.nodeType === Node.ELEMENT_NODE ? (node as HTMLElement) : null;

      if (!nodeElement) continue;

      const slotName = nodeElement.getAttribute
        ? nodeElement.getAttribute('slot')
        : null;

      switch (slotName) {
        case HTMLBnumColumn.#_SLOTS.HEADER:
          this.#_processNode(
            nodeElement,
            HTMLBnumColumn.#_CLASSES.CONTENT.HEADER,
          );
          headerContainer.appendChild(node);

          if (!hasHeader) hasHeader = true;
          break;

        case HTMLBnumColumn.#_SLOTS.FOOTER:
          this.#_processNode(
            nodeElement,
            HTMLBnumColumn.#_CLASSES.CONTENT.FOOTER,
          );
          footerContainer.appendChild(node);

          if (!hasFooter) hasFooter = true;
          break;
        default:
          this.#_processNode(
            nodeElement,
            HTMLBnumColumn.#_CLASSES.CONTENT.BODY,
          );
          bodyContainer.appendChild(node);
          break;
      }
    }

    // Nettoyage du container principal
    while (container.firstChild) {
      container.removeChild(container.firstChild);
    }

    // Ajout des classes principales
    this.classList.add(
      HTMLBnumColumn.#_CLASSES.HOST,
      `${HTMLBnumColumn.#_CLASSES.CONTENT_PREFIX}--${this.type}`,
    );

    // Injection conditionnelle dans le DOM
    if (hasHeader) fragment.appendChild(headerContainer);

    if (this.#_keepBody) fragment.appendChild(bodyContainer);
    else fragment.append(...bodyContainer.childNodes);

    if (hasFooter) fragment.appendChild(footerContainer);

    container.appendChild(fragment);
  }

  /**
   * Reactivity for Type attribute change
   */
  protected _p_update(
    name: string,
    oldVal: Nullable<string>,
    newVal: Nullable<string>,
  ): void {
    if (oldVal === newVal) return;

    if (name === HTMLBnumColumn.#_ATTRIBUTES.TYPE && this.alreadyLoaded) {
      if (oldVal)
        this.classList.remove(
          `${HTMLBnumColumn.#_CLASSES.CONTENT_PREFIX}--${oldVal}`,
        );
      if (newVal)
        this.classList.add(
          `${HTMLBnumColumn.#_CLASSES.CONTENT_PREFIX}--${newVal}`,
        );
    }
  }

  //#endregion LifeCycle

  //#region Méthodes privées
  /**
   * Traite un élément enfant : supprime l'attribut slot, ajoute les classes CSS nécessaires,
   * et gère la rétrocompatibilité des classes "header".
   * @param {HTMLElement} element L'élément à traiter
   * @param {string} specificClass Classe CSS spécifique à ajouter
   * @private
   */
  #_processNode(element: HTMLElement, specificClass: string): void {
    element.removeAttribute('slot');
    element.classList.add(specificClass, HTMLBnumColumn.#_CLASSES.FROM_SLOT);

    // Gestion legacy "header" class duplication
    if (element.classList.contains(HTMLBnumColumn.#_CLASSES.HEADER.RC)) {
      element.classList.remove(HTMLBnumColumn.#_CLASSES.HEADER.RC);
      element.classList.add(HTMLBnumColumn.#_CLASSES.HEADER.OLD);
    }
  }
  //#endregion Méthodes privées

  //#region Static Methods
  /**
   * Méthode interne pour définir les attributs observés.
   * @returns Attributs à observer
   */
  protected static _p_observedAttributes(): string[] {
    return [this.#_ATTRIBUTES.TYPE];
  }
  //#endregion Static Methods
}
