import { EMPTY_STRING } from '@rotomeca/utils';
import BnumElement from '../../bnum-element';
import { Define, SetAttr } from '../../../core/decorators';
import { TAG_FOLDER_LIST } from '../../../core/utils/constants/tags';

@Define()
export class HTMLBnumFolderList extends BnumElement {
  constructor() {
    super();
  }

  @SetAttr('role', 'group')
  protected _p_preload(): void {}

  protected _p_isShadowElement(): boolean {
    return false;
  }

  static Write(
    content: string = EMPTY_STRING,
    attrs: Record<string, string> = {},
  ): string {
    const attributes = this._p_WriteAttributes(attrs);
    return `<${this.TAG} ${attributes}>${content}</${this.TAG}>`;
  }

  static get TAG() {
    return TAG_FOLDER_LIST;
  }
}
